.people {
    margin-top: 120px;
    margin-left: 220px;
    width: 850px;
}
.people .searchbox::placeholder {
    font-family: "GTEestiProDisplay-Thin";
    color: #7b7b7b;
    font-size: 18px;
    letter-spacing: 0.1px;
}
.people .searchicon {
    float: right;
    position: absolute;
    margin-top: 10px;
    margin-left: -55px;
    height: 22px;
    width: 25px;
}
.people .button1 {
    float: right;
    width: 80px;
    height: 25px;
    border: none;
    border-radius: 4px;
    background-color: #0052cc;
    color: #fff;
    font-family: "SegoeUISemibold";
    margin-top: -30px;
    font-size: 11px;
}

.people p {
    font-family: "SegoeUISemibold";
    color: #141414;
}
.people .button2 {
    margin-top: -30px;
    margin-right: 90px;
    width: 80px;
    height: 25px;
    float: right;
    font-size: 11px;
    border-radius: 4px;
    border: none;
    background-color: #efefef;
    color: #8690a3;
    font-family: "SegoeUISemibold";
}
.people .button1:hover {
    background-color: #003d99;
    cursor: pointer;
}
.people .button2:hover {
    cursor: pointer;
}
.peoples {
    margin-top: 60px;
    margin-left: 6px;
    width: 1000px;
}
.people1 {
    border: solid 0.3px rgba(112, 112, 112, 0.3);
    width: 125px;
    height: 150px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 6px;
    float: left;
    margin-left: 15px;
}
.people1 span {
    margin-top: 17px;
    font-family: "GTEestiProDisplay-Light";
    color: #344563;
    margin-bottom: -4px;
    font-size: 10px;
}
.people1 p {
    font-family: "SegoeUI";
    color: #7b7b7b;
    font-size: 8px;
}
